//hero-sec

.hero-sec {
  background-color: $pri;
  box-shadow: inset 0 8px 8px -8px rgb(0 0 0 / 30%), inset 0 -8px 8px -8px rgb(0 0 0 / 30%);

  padding: 3rem;
  .flex {
    padding: 1rem 3rem;
  }
  h1 {
    color: white;
    font-size: 3rem;
    padding-bottom: 20px;
  }
  h2 {
    color: white;
    font-size: 2rem;
  }
  p{
    font-size: 1.5rem;
  }
}

//tags-sec
.tags-sec {
  background-color: $gray;

  & > * {
    padding: 1rem;
  }
  h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 800;
    margin-bottom: 1rem;
  }
  ul {
    li {
      margin: .5rem;
      border-radius: 5px;
      a {
        background-color: $pri;
        color: wheat;
      }
      a:hover {
        background-color: green;
        color: white;
      }
      .btn-sec {
        font-size: 0.7em;
        padding: 0.2rem 0.5rem;
      }
    }
  }
}



//home-tags-div

.home-tags-div {
  .active {
    border-bottom: 2px solid $pri;
  }
  li {
    margin-right: 1rem;
  }
}

//home-article-card

.home-article-card {
  padding: 1rem;
  border-bottom: 1px solid rgb(209, 206, 206);
  & > * {
    margin-bottom: 0.8rem;
  }
  .artilce-card-header-img {
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
  }
  .article-card-author {
    padding-left: 0.5rem;
    h5 {
      margin-bottom: 0.3rem;
      color: $pri;
    }
    h6 {
      font-size: 0.8rem;
      color: $text-gray;
    }
  }
  .article-card-likes {
    padding: 0.5rem;
    border: 1px solid $pri;
    border-radius: 5px;
    svg {
      cursor: pointer;
    }
    & > * {
      color: $pri;
      margin-right: 0.4rem;
    }
  }
  .article-card-body {
    h3 {
      font-size: 1.5rem;
      font-weight: 700;
    }
    h5 {
      font-size: 1rem;
      color: $text-gray;
      margin-top: 0.5rem;
    }
  }
  .article-card-footer {
    a:hover {
      color: white;
      background-color: green;
    }
    .article-tag {
      padding: 0.5rem 0.5rem;
      margin-right: 0.3rem;
      font-size: 0.7rem;
      font-weight: 600;
      border-radius: 10px;
      background-color: rgba(214, 211, 211, 0.973);
      color: rgba(0, 0, 0, 0.418);
    }
  }
}
.tags{
  border-radius: 5px;
  margin: 2px;
  a{
    background-color: rgb(206, 202, 202) !important;
    color: black !important;
    padding: 2px;
  }
}